.form {
  background-color: #ffffff;
  padding: 0 10px 11px 10px;
  text-align: center;

  @media (max-width: @mobile-width-only) {
    margin-bottom: 35px;
    text-align: left;
  }

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    margin: 0 auto 52px;
    padding: 6px 30px 15px 30px;
    line-height: 22px;
  }

  @media (min-width: @desktop-width) {
    margin: -10px auto 55px;
    padding: 0 10px 15px 10px;
    line-height: 22px;
  }
}

.main-form {
  @media (max-width: @mobile-width-only) {
    width: 300px;
    margin: auto;
  }

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    display: -ms-grid;
    display: grid;
    width: 708px;
    margin: auto;
    -ms-grid-columns: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    -ms-grid-rows: 360px 310px 310px 310px;
    grid-template-rows: 360px 310px 310px 310px;
  }

  @media (min-width: @desktop-width) {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    -ms-grid-rows: 360px 214px 215px 310px;
    grid-template-rows: 360px 214px 215px 310px;
  }
}

.main-form__block {
  border: none;
  box-sizing: border-box;
  border-bottom: 1px solid #efefef;
  margin: auto;
  padding-bottom: 30px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    margin-top: 0;
    padding-top: 0;
    border: none;
  }

  @media (min-width: @desktop-width) {
    margin-top: 0;
    padding-top: 0;
    border: none;
  }
}

.main-form__fieldset-legend {
  font-size: 18px;
  font-weight: 700;
  padding: 17px 0 0 0;
  margin: 0 0 12px 0;
  text-align: center;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    text-align: left;
    margin: auto;
  }

  @media (min-width: @desktop-width) {
    font-size: 22px;
    text-align: left;
    margin: auto;
  }
}

.label {
  font-weight: 400;
  margin-right: 10px;
}

.input-block + .input-block {
  margin-top: 18px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    margin-top: 20px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 20px;
  }
}

.name-form {
  grid-column-start: 2;
  grid-column-end: -1;
  max-width: 300px;
  padding: 0 0 27px 11px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    margin: 0 0 0 0;
    padding: 0 0 30px 58px;
    max-width: 324px;
  }

  @media (min-width: @desktop-width) {
    margin: 3px 0 0 70px;
    letter-spacing: 0.2px;
  }
}

.name-form .input-block + .input-block {
  @media (max-width: @mobile-width-only) {
    margin-top: 20px;
  }
}

.name-form .input-block {
  @media (min-width: @desktop-width) {
    width: 400px;
  }
}

.name-form .main-form__fieldset-legend {
  margin: 0 auto 17px auto;
  padding: 22px 0 0 0;
  letter-spacing: 0.6px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    font-size: 22.2px;
    margin: 0 0 55px 0;
    padding: 17px 0 0;
    letter-spacing: normal;
  }

  @media (min-width: @desktop-width) {
    margin: 0 0 55px 0;
    padding: 17px 0 0;
    letter-spacing: normal;
  }
}

