.name-form__label {
  padding: 22px 2px 0 0;
}

input[type="text"],
input[type="tel"],
input[type="email"] {
  width: 100%;
  display: inline-block;
  border: none;
  height: 58px;
  box-sizing: border-box;
  padding: 18px 18px 14px 23px;
  background-color: #f2f2f2;

  font-weight: 700;
  font-size: 15px;
  outline: none;

  @media (max-width: @mobile-width-only) {
    padding: 18px 18px 11px 25px;
  }

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    font-size: 16px;
    padding: 27px 18px 15px 25px;
  }
}

.name-form__firstName-block,
.name-form__secondName-block,
.name-form__middleName-block {
  max-height: 58px;
  display: flex;
}

.contacts-form {
  grid-column-start: 1;
  grid-column-end: -1;
  padding: 5px 11px 26px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    width: 679px;
    margin: 0 0 30px 0;
    padding: 0 0 30px 31px;
    text-align: left;
  }

  @media (min-width: @desktop-width) {
    width: 985px;
    height: 170px;
    margin: 0 85px 30px 86px;
    padding: 0 0 30px 0;
  }
}

.contacts-form .input-block + .input-block {
  @media (max-width: @mobile-width-only) {
    margin-top: 12px;
  }

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    margin-top: 25px;
  }
}

.contacts-form__phone-block {
  @media (min-width: @desktop-width) {
    display: inline-block;
    width: 425px;
    margin: 0 51px 0 0;
  }
}

.contacts-form__email-block {
  @media (min-width: @desktop-width) {
    display: inline-block;
    width: 502px;
    box-sizing: border-box;
  }
}

.contacts-form .main-form__fieldset-legend {
  position: relative;
  font-size: 18px;
  text-align: center;
  margin: 0 auto 12px auto;
  padding-top: 20px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    font-size: 21px;
    padding: 24px 0 0 0;
    margin: 0 auto 24px auto;
  }

  @media (min-width: @desktop-width) {
    font-size: 21px;
    padding: 25px 0 0 21px;
    margin-bottom: 52px;
  }
}

.contacts-form .main-form__fieldset-legend::before,
.contacts-form .main-form__fieldset-legend::after {
  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    content: "";
    position: absolute;
    display: inline-block;
    width: 123px;
    height: 2px;
    background-color: #dbdbdb;
  }

  @media (min-width: @desktop-width) {
    content: "";
    position: absolute;
    display: inline-block;
    width: 280px;
    height: 2px;
    background-color: #dbdbdb;
  }
}

.contacts-form .main-form__fieldset-legend::before {
  bottom: 13px;
  left: -165px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    left: -185px;
  }

  @media (min-width: @desktop-width) {
    left: -330px;
  }
}

.contacts-form .main-form__fieldset-legend::after {
  bottom: 13px;
  right: -185px;

  @media (min-width: @desktop-width) {
    right: -330px;
  }
}

.contacts-form__label {
  display: inline-block;
  margin: 2px 0 10px 21px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    margin: 0 auto 10px 21px;
  }

  @media (min-width: @desktop-width) {
    margin-right: 13px;
  }
}

.contacts-form__input[type="tel"],
.contacts-form__input[type="email"] {
  background-image: url("../../img/icons/icon-phone.svg");
  background-repeat: no-repeat;
  background-position: 93% 50%;
  padding: 4px 50px 0 23px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    background-position: 97% 50%;
    padding: 5px 50px 0 19px;
  }

  @media (min-width: @desktop-width) {
    width: 317px;
  }
}

.contacts-word {
  @media (min-width: @desktop-width) {
    display: none;
  }
}

.contacts-form__input[type="email"] {
  background-image: url("../../img/icons/icon-mail.svg");

  @media (min-width: @desktop-width) {
    width: 317px;
  }
}

.impression-form {
  padding: 5px 11px 25px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
    width: 305px;
    margin: 0 0 0 13px;
    padding: 0 0 0 0;
  }

  @media (min-width: @desktop-width) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
    width: 429px;
    margin: 0 0 18px 33px;
    padding: 1px 0 30px 0;
  }
}

.impression-form .main-form__fieldset-legend {
  font-size: 18px;
  margin: 0 auto 33px auto;
  padding: 20px 0 0;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    font-size: 21px;
    margin: 0 0 76px 0;
    padding: 17px 0 0 16px;
  }

  @media (min-width: @desktop-width) {
    font-size: 21px;
    margin: 0 0 75px 0;
    padding: 19px 0 0 157px;
  }
}

.impression-form__block:not(:last-child) {
  margin-bottom: 45px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    margin: 0 0 55px 0;
  }

  @media (min-width: @desktop-width) {
    margin: 0 0 56px 0;
  }
}

.impression-form__block:last-child {
  margin-bottom: 15px;
}

.impression-form__negative-block {
  margin-bottom: 30px;
  padding-top: 10px;
}

.impression-form__label {
  position: relative;
  font-size: 18px;
  font-weight: 700;
  text-transform: none;
  padding-left: 78px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    font-size: 18px;
    padding-left: 70px;
  }

  @media (min-width: @desktop-width) {
    font-size: 18px;
    padding-left: 70px;
    margin: 0;
  }
}

.npsb-form {
  display: none;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    display: inline-block;
  }

  @media (min-width: @desktop-width) {
    display: inline-block;
  }
}

.impression-form__label::before {
  content: "";
  position: absolute;
  top: -16px;
  left: -1px;
  display: inline-block;
  width: 56px;
  height: 56px;
  background-color: #f2f2f2;
  border-radius: 50%;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    top: -16px;
    left: -6px;
    width: 56px;
    height: 56px;
  }

  @media (min-width: @desktop-width) {
    top: -16px;
    left: -6px;
    width: 56px;
    height: 56px;
  }
}

.impression-form__input:checked + .impression-form__label::after {
  content: "";
  position: absolute;
  top: 1px;
  left: 16px;
  display: inline-block;
  width: 22px;
  height: 22px;
  background-color: #333333;
  border-radius: 50%;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    top: 2px;
    left: 13px;
    width: 22px;
    height: 22px;
  }

  @media (min-width: @desktop-width) {
    top: 4px;
    left: 13px;
    width: 22px;
    height: 22px;
  }
}

.place-form {
  grid-column-start: 1;
  grid-column-end: -1;
  text-align: center;
  padding: 5px 11px 10px;

  @media (max-width: @mobile-width-only) {
    text-align: left;
  }

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    padding: 5px 11px 10px 2px;
  }

  @media (min-width: @desktop-width) {
    width: 1000px;
    margin: 0 100px 0 100px;
    padding: 0 0 30px 0;
  }
}

.place-form__wrapper {
  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    width: 500px;
    padding: 30px 0 0 43px;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    width: 1000px;
    padding: 33px 0 0 5px;
  }
}

.place-form .input-block + .input-block {
  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    margin: 0;
  }
}

.place-form .main-form__fieldset-legend {
  margin: 0 0 34px -3px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    position: relative;
    max-width: 300px;
    text-align: center;
    line-height: 25px;
    font-size: 21px;
    margin: 0 auto 28px auto;
    padding: 16px 0 0 0;
  }

  @media (min-width: @desktop-width) {
    position: relative;
    width: 300px;
    text-align: center;
    line-height: 25px;
    font-size: 21px;
    margin: 0 auto 29px auto;
    padding: 17px 0 0 0;
  }
}

.place-form .main-form__fieldset-legend::before,
.place-form .main-form__fieldset-legend::after {
  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    content: "";
    position: absolute;
    display: inline-block;
    width: 123px;
    height: 2px;
    background-color: #dbdbdb;
  }

  @media (min-width: @desktop-width) {
    content: "";
    position: absolute;
    display: inline-block;
    width: 280px;
    height: 2px;
    background-color: #dbdbdb;
  }
}

.place-form .main-form__fieldset-legend::before {
  bottom: 21px;
  left: -165px;

  @media (min-width: @desktop-width) {
    left: -330px;
  }
}

.place-form .main-form__fieldset-legend::after {
  bottom: 21px;
  right: -165px;

  @media (min-width: @desktop-width) {
    right: -330px;
  }
}

.place-form__label {
  position: relative;
  font-size: 18px;
  font-weight: 700;
  text-transform: none;
  margin: 0 0 0 0;
  padding: 0 0 0 78px;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    font-size: 18px;
  }

  @media (min-width: @desktop-width) {
    font-size: 18px;
    padding-left: 65px;
  }
}

.place-form__label::before {
  content: "";
  position: absolute;
  top: -17px;
  left: -1px;
  display: inline-block;
  width: 56px;
  height: 56px;
  border-radius: 5px;
  background-color: #f2f2f2;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    top: -16px;
    left: -2px;
    width: 56px;
    height: 56px;
  }

  @media (min-width: @desktop-width) {
    top: -16px;
    left: -14px;
    width: 56px;
    height: 56px;
  }
}

.place-form__block {
  height: 54px;
  margin: 0 0 0 0;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    height: 60px;
  }

  @media (min-width: @desktop-width) {
    width: 220px;
    margin-right: 35px;
  }
}

.place-form__wrapper .place-form__block:first-child {
  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    margin-right: 59px;
  }
}

.place-form__wrapper .place-form__block:first-child,
.place-form__wrapper .place-form__block:nth-child(2) {
  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    width: 200px;
    height: 88px;
  }

  @media (min-width: @desktop-width) {
    width: 220px;
    margin-right: 35px;
  }
}

.place-form__wrapper .place-form__block:nth-child(3) {
  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    width: 179px;
    margin-right: 90px;
  }

  @media (min-width: @desktop-width) {
    width: 211px;
    margin-right: 35px;
  }
}

.place-form__wrapper .place-form__block:nth-child(4) {
  @media (min-width: @desktop-width) {
    width: 211px;
    padding: 0 0 0 8px;
  }
}

.place-form__block + .place-form__block {
  @media (max-width: @mobile-width-only) {
    margin-top: 26px;
  }

  @media (min-width: @desktop-width) {
    margin: 0;
  }
}

.place-form__input:checked + .place-form__label::after {
  content: "";
  position: absolute;
  top: 4px;
  left: 16px;
  width: 23px;
  height: 16px;
  display: inline-block;
  background-image: url("../../img/icons/icon-tick.svg");
  transition: 1s;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    top: 3px;
    left: 16px;
  }

  @media (min-width: @desktop-width) {
    top: 2px;
    left: 0;
  }
}

